!function(){"use strict";jQuery(document).ready(function(u){if(0!=u(".product.product-type-variable").length)u(document).on("found_variation","form.cart",function(t,e){let i=u(".product .summary .product_title.entry-title").text();if(e.attributes){i+=" - ";let t=[];for(var r in e.attributes)t.push(e.attributes[r]);i+=t.join(", ")}gtag("event","view_item",{value:e.display_price,currency:"USD",items:[{item_id:String(e.variation_id),item_name:i}]})});else{const t=u(".product .summary .price .amount > bdi").text(),e=u("form.cart button[type=submit]").val(),i=u(".product .summary .product_title.entry-title").text();gtag("event","view_item",{value:Number(t.replace(/[^0-9.]+/g,"")),currency:"USD",items:[{item_id:e,item_name:i}]})}})}(jQuery);